.homeWrap {
    .article2 {
        .row {
            .local {
                display:flex;
                flex-wrap:wrap;
                padding:30px 0;
                li {
                    width:33.33%; padding:40px; 
                    a { display:flex; align-items:center;
                      
                        img { width:100%; margin-right:10px; border-radius:10px;
                             border: 1px solid transparent;opacity: 0.8;
                            &:hover { opacity: 1; border: 1px solid #000;}
                         }
                    }
                }
            }
            .lifeStyle {
                h2 { font-size:20px }
                .slide {
                    position: relative;
                   .hov {
                       position: absolute; bottom: 0; left: 0; top: 0; right: 0;
                       opacity: 0;
                       background-color: rgba(0, 0, 0, 0.5);
                       color: #fff;
                       .info {position: absolute; bottom: 0; left: 0;
                        a:focus { color: red;}
                    }
                   }
                   &:hover .hov { opacity: 1;}
                   
                }
                position: relative;
                .slick-arrow { position:absolute; top:50%; transform:translateY(-50%);
                    background:none; padding:10px; z-index:99999; width:60px; height:60px; color:#000;
                    &.slick-prev { right: -80px;  }
                    &.slick-next { left: -80px;  }    
                    }
            }
        }
    }

    .article3 { background:#000; padding:100px 0; color:#fff;
        .row { display:flex; justify-content:space-between; 
            div.rowchild { width:100%;
                // img { border-radius:40px 40px 0px 0px; }
                p { background:#999; 
                    // border-radius:0px 0px 40px 40px;
                    padding:20px; 
                }
                &.right { width:46%;
                    display:flex; flex-direction:column;
                    justify-content:space-between;
                    .rightRow1 { display:flex; justify-content:space-between;
                        div { width:48% }
                    }
                }
            }
        }
    }
}